import React, { Fragment, Component } from 'react';

import Auth from './pages/Auth'
import Cabinet from './pages/Cabinet'
import BlankPage from './pages/Blank'

import api from './util/api'

import './assets/styles/index.css'

const InitialDataContext = React.createContext({
    token: null,
    setToken: () => {},
    user: null ,
    setUser: () => {},
    preferences: [],
    setPreferences: () => {}
})

class AppWrap extends Component {

    constructor(props) {
        super(props)

        this.state = {
            user: null,
            loading: true,
            preferences: [],
        }
    }

    componentDidMount() {
        this.setToken(localStorage.getItem('Access-Token'))
    }

    setToken = (token) => {

        if (token) {
            localStorage.setItem('Access-Token', token)
            api.defaults.headers.common['Access-Token'] = token;

        } else {
            localStorage.removeItem('introViewed')
            localStorage.removeItem('Access-Token')
            delete api.defaults.headers.common['Access-Token'];
        }
        this.setState({
            token,
            loading: false
        })
    }

    setUser = (user) => {
        this.setState({
            user
        })
    }

    setPreferences = (preferences, append) => {

        let newTags = [...preferences]

        if (append) {
            newTags = [...this.state.preferences, ...preferences]
        }
        const unique = [...new Set(newTags)]

        this.setState({
            preferences: unique
        })
    }


    render() {

        const { token, user, loading, preferences } = this.state

        if (loading) {
            return null
        }

        return (
            <InitialDataContext.Provider value={{ token, user, preferences,
                setToken: this.setToken,
                setUser: this.setUser,
                setPreferences: this.setPreferences
            }}>
                <Fragment>
                    {window.innerWidth > 767 ? <BlankPage /> :
                        <App token={token} user={user} preferences={preferences}
                             setToken={this.setToken}
                             setPreferences={this.setPreferences}
                             setUser={this.setUser}/>
                    }
                </Fragment>
            </InitialDataContext.Provider>
        )
    }
}

const App = ({token, setToken, user, preferences, setPreferences, setUser}) => {

    if (!token) {
        return (
            <Fragment>
                <Auth setToken={setToken}/>
            </Fragment>
        )
    }

  return (
      <Fragment>
          <Cabinet user={user} preferences={preferences} setToken={setToken}
                   setUser={setUser} setPreferences={setPreferences}/>
      </Fragment>
  );
}

export default AppWrap;
