import React, { Component, Fragment } from 'react';

import api from '../../../util/api'
import style from './preferences.module.css'

import defaultAvatar from '../../../assets/img/preferences/avatar.png'
// import defaultAvatar from '../../../assets/img/preferences/default-user-avatar.svg'
import confirm from '../../../assets/img/preferences/confirm.svg'
import { ReactComponent as Confirm } from "../../../assets/img/confirm.svg";
import { ReactComponent as Bin } from "../../../assets/img/bin.svg";


const Tag = ({title, onClick, active}) => {
    return (
        <div className={`${style.tag} ${active ? style.tagActive : ''}`} onClick={onClick}>
            # {title}
        </div>
    )

}

class Preferences extends Component {

    constructor(props) {

        super(props)

        this.state = {
            pending: false,
            addingTags: false,
            tagsLoading: !props.preferences,
            tagsText: '',
            tags: props.preferences,
            tagsToRemove: []
        }

        console.log(props.preferences, 'props preferences')
    }

    componentDidMount() {

        this.props.updatePreferences()

        if (!this.props.preferences) {
            setTimeout(() => {
                this.setState({
                    tagsLoading: false
                })
            }, 30000)
        }
    }

    onKeyDown = (event) => {
      // 'keypress' event misbehaves on mobile so we track 'Enter' key via 'keydown' event
      if (event.key === 'Enter') {
        event.preventDefault();
        event.stopPropagation();
        this.setTags()
      }
    }

    setTags = () => {

        const { tagsText } = this.state

        const tagsFromForm = tagsText.split(',').filter((item) => {
            return item.trim().length > 0
        }).map((item) => {
            return item.replace('#', '')
        })


        if (!tagsFromForm.length) {
            return
        }

        let tags = [...this.state.tags, ...tagsFromForm]


        const unique = [...new Set(tags)];


        this.setState({
            addingTags: false,
            tagsText: '',
            tags: unique
        })
    }

    handleRemoveList = (tag) => {

        const { tagsToRemove } = this.state

        let newTags = [...tagsToRemove]

        if(tagsToRemove.includes(tag.trim())) {
            newTags = newTags.filter((item) => {

                return item.trim() !== tag.trim()
            })

        } else {
            newTags = [...newTags, tag]
        }

        this.setState({
            tagsToRemove: newTags
        })

    }

    onConfirm = () => {
        const { tags } = this.state


        if (!tags.length) {
            return
        }

        const unique = [...new Set(tags)].filter((item) => {
            return !this.props.preferences.includes(item)
        });

        if (unique.length) {
            Promise.all(unique.map(this.postTag)).then((res) => {
                this.props.updatePreferences()
                this.props.onSetScreen()
            }).catch((err) => {
                console.log(err, 'err')
            })
        } else {
            this.props.onSetScreen()
        }
    }

    removePreference = () => {

        const { tagsToRemove, tags } = this.state
        const { preferences } = this.props

        this.setState({
            pending: true
        })

        let remote = []

        tagsToRemove.forEach((tag) => {
            if (preferences.includes(tag)) {
                remote.push(tag)
            }
        })

        if (remote.length) {
            api.delete(`user/preferences`, { data:{
                    preferences: remote
                }}).then(() => {
                this.props.updatePreferences()
            }).catch(() => {
                this.props.updatePreferences()
            })
        }

        const newTags = tags.filter((tag) => {
            return !tagsToRemove.includes(tag)
        })

        this.setState({
            tags: newTags,
            tagsToRemove: [],
            pending: false
        })

    }

    postTag(tag) {

        return api
            .post(`/user/preferences`, {
                preference: tag
            })
            .then(function(response) {
                return {
                    success: true,
                    data: response.data
                };
            })
            .catch(function(error) {
                return { success: false };
            });
    }


    render() {
        const { user } = this.props
        const { tags, tagsText, addingTags, pending, tagsLoading, tagsToRemove } = this.state

        if (!user) {
            return null
        }

        return (
            <Fragment>
                <header className={style.header}>
                    <div className={style['avatar-ctn']}>
                        <div className={style.avatar}>
                            <div className={style['avatar-img-wrap']} style={{
                                backgroundImage: `url(${user.profile_photo || defaultAvatar})`
                            }} />
                        </div>
                    </div>
                    <div className={style.user}>
                        <h2 className="title">
                            Привет {user.username}!
                        </h2>
                        <p className="text text--thin">
                            Введите личные интересы, и мы подберем вам персональные рекомендации
                        </p>
                    </div>
                </header>
                <div className={`${style.main}`}>
                    <div className="main-content content ">
                        <div className="content-body">
                            <div className={style.logout}>
                                <button className='button-clear-defaults' onClick={() => {
                                    this.props.setToken(null)
                                    this.props.setUser(null)
                                }}>
                                    Выход
                                </button>
                            </div>

                            {!tagsLoading && <Fragment>
                                {tags.length ?
                                    <div>
                                        {tags.map((tag, i) => {

                                            const active = tagsToRemove.includes(tag)

                                            return <Tag title={tag}
                                                        onClick={() => {
                                                            this.handleRemoveList(tag)
                                                        }}
                                                        active={active}
                                                        key={`${tag}-${i}`}/>
                                        })}

                                        <button className={style.more}
                                                disabled={pending}
                                                onClick={() => {
                                                    this.setState({
                                                        addingTags: !addingTags
                                                    })
                                                }}>
                                            <span className={style.plus}>+</span>
                                        </button>

                                    </div>
                                    : <Fragment>
                                    <span className="text">
                                            Пока нет тегов дальше нельзя
                                        </span>
                                        <button className={style.more}
                                                disabled={pending}
                                                onClick={() => {
                                                    this.setState({
                                                        addingTags: !addingTags
                                                    })
                                                }}>
                                            <span className={style.plus}>+</span>
                                        </button>
                                </Fragment>
                                }

                                {addingTags &&  <div className={style.moreInput}>
                                   <input
                                       autoFocus
                                       onKeyDown={this.onKeyDown}
                                       disabled={pending}
                                       placeholder={'Например: клуб'}
                                       onChange={(e) => {
                                           this.setState({
                                               tagsText: e.target.value
                                           })
                                       }}

                                       className={`textarea ${style.textarea}`}
                                       value={tagsText} />
                                    <button
                                        disabled={pending || !tagsText.trim().length}
                                        className={style.moreSave} onClick={this.setTags}>
                                        <img src={confirm} alt=""/>
                                    </button>
                                </div>}
                            </Fragment>}
                        </div>

                    </div>
                   <footer className={style.footer}>
                       {tagsLoading ?
                       <Fragment>
                           <div className={style.loadingWrap}>
                               <div className={`${style.loadingItem} loadingItem1`} />
                               <div className={`${style.loadingItem} loadingItem2`} />
                               <div className={`${style.loadingItem} loadingItem3`} />
                           </div>
                       </Fragment>
                           : <Fragment>
                               <div className={style.moreTags}>
                                   <button disabled={!tagsToRemove.length || pending}
                                           onClick={this.removePreference}
                                           className={style.remove}>
                                       <Bin />
                                   </button>
                               </div>
                               <div className={`${style.confirm} ${pending || !tags.length ? style.confirm_opacity : ''}`}>
                                   <button className={style.confirmBtn}
                                           disabled={pending || !tags.length}
                                           onClick={this.onConfirm}>
                                       <Confirm />
                                   </button>
                               </div>
                           </Fragment>
                       }
                   </footer>

                </div>
            </Fragment>
        )
    }
}

export default Preferences