import React, { Fragment, Component } from 'react';
import style from "../../../components/SuccessPage/success.module.css";

import CircleBtn from "../../../components/CircleBtn";

import api from "../../../util/api";
import faceLike from "../../../assets/img/recommendation/face-like.png";
import randomicon from "../../../assets/img/icon.svg";
import faceDislike from "../../../assets/img/recommendation/face-dislike.png";
import happyFace from "../../../assets/img/recommendation/happy-fave.svg";
import sadFace from "../../../assets/img/recommendation/sad-face.svg";

class RecommendationList extends Component {

    constructor(props) {
        super(props);

        this.state = {
            recommendation: null,
            pending: true,
            rate: false,
            detailedView: false,

        }
    }


    setRate = (rate = true) => {
        this.setState({
            rate
        })
    }

    render() {

        const { rate } = this.state


        if (this.props.subscribeSlide) {

            return  (
                <div className={style['recommendation-over']}>
                    <h2 style={{
                        fontSize: '1.85rem',
                        marginBottom: '20px',
                        fontWeight: 300
                    }}>
                        Рекомендации закончились.
                        к сожалению ничего не нашлось
                    </h2>
                    <p  style={{
                        fontSize: '1.12rem',
                        marginBottom: '30px'
                    }}>
                        Подпишись, чтобы получать уникальные события.
                    </p>
                    <p style={{
                        fontSize: '4rem',
                        marginBottom: 0,
                        marginTop: 0,
                        lineHeight: 1
                    }}>
                        25р
                    </p>
                    <p style={{
                        fontSize: '1rem',
                        marginBottom: '30px',
                    }}>
                        в месяц
                    </p>

                    <button className={style.subsribeBtn}>
                        Подписаться
                    </button>
                </div>
            )
        }


        const hasPhoto = !!this.props.item.mainphoto && !this.props.item.mainphoto.includes('undefined')

        return (
            <div className={style.wrap} style={{
                backgroundImage: hasPhoto ? `url(${this.props.item.mainphoto})` : ' url(https://image.freepik.com/free-photo/abstract-blur-hotel-interior_1203-8529.jpg)'
            }}>
                {rate &&
                    <div className={style.rate}>
                        <div className={style['rate-title']}>
                            rate this point
                        </div>
                        <div>
                            <a href="#" className={style['link-white']} onClick={(e) => {
                                e.preventDefault()
                                this.setRate(false)
                            }}>
                                rate later
                            </a>
                        </div>
                        <div className={style['rate-faces']}>
                            <button className={style['rate-face-btn']} onClick={() => {
                                this.setRate(false)
                            }}>
                                <img src={happyFace} alt=""/>
                            </button>
                            <div className={style['rate-faces-sep']}>
                                or
                            </div>
                            <button className={style['rate-face-btn']} onClick={() => {
                                this.setRate(false)
                            }}>
                                <img src={sadFace} alt=""/>
                            </button>
                        </div>

                        <div style={{
                            marginTop: 'auto',
                            marginBottom: '20px',
                        }}>
                            <a href="#" className={style['link-pink']} onClick={(e) => {
                                e.preventDefault()

                                api.post(`recommendation/skip`, {
                                    recommendationId: this.props.id
                                }).then(({data}) => {
                                    this.setState({
                                        recommendation: data.data,
                                        pending: false
                                    })
                                })


                                this.setRate(false)
                            }}>
                                skip
                            </a>
                        </div>
                    </div>
                }
                <div className={style.content}>
                    {this.props.random && 
                        <div>
                            <div className={style.random}>
                               <img className={style.randomicon} src={randomicon} alt=""/>
                               <h2 className={style.randomtext}>Посмотри популярные места, пока мы ищем рекомендации под твой запрос.</h2>
                            </div> 
                            <hr className={style.colored}/>
                        </div> 
                    }
                    <div className={style['content-inner']}>

                        <Fragment>
                            <div className={style['content-top']}>
                                <div className={style.col}>
                                    <div className={style.title}>
                                        {this.props.item.name}
                                    </div>
                                    <div className={style.workHours}>
                                        {this.props.item.workhours}
                                    </div>
                                </div>
                                <div className={style['right-col']}>
                                    <div className={style.rating}>
                                        <div>
                                            Рейтинг места
                                        </div>
                                        <div>
                                            <button className='button-clear-defaults'
                                                    ><img src={faceLike} alt=""/></button> +  {this.props.positiveReviews || 'N/A'}
                                        </div>
                                        <div>
                                            <button className='button-clear-defaults'
                                                  ><img src={faceDislike} alt=""/></button> - {this.props.negativeReviews || 'N/A'}
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className={style['content-bottom']}>
                                <div className={`${style['content-bottom-item']} ${style.cost}`}>
                                    <ul className={style['tags']}>
                                    {this.props.tags.map((tag,i) => {
                                        return (
                                            <li className={style['tag']} key={i}>{tag}</li>
                                        )
                                    })}
                                    </ul>
                                    {this.props.averageBillUSD > 0 ? `средний чек: ${this.props.averageBillUSD}₽` : 'FREE'}
                                    <div className={style.visited}>
                                        <div className={style['compare-card-photo--inner']}>
                                            {this.props.lastFiveUsersSelectedThisLocation.map((account, i) => {

                                                return (
                                                    <div className={style['compare-card-photo--image']} key={i}>
                                                        <div className={style['avatar-img-wrap']} >
                                                            <img src={account} className={`${style['avatar-img']}`} />
                                                        </div>
                                                    </div>

                                                )
                                            })}
                                        </div> <div style={{
                                        marginLeft: '15px'
                                    }}>
                                        +{this.props.numberOfUsersSelectedThisLocation}
                                    </div>
                                    </div>
                                </div>
                                <div className={style['content-bottom-item']}>
                                    <CircleBtn size={60} fontSize={'0.67rem'}
                                               onClick={() => {
                                                   this.props.setCurrentRecomendation(this.props)
                                               }}
                                               style={{
                                                   fontWeight: 300
                                               }}>
                                        Вперед 
                                    </CircleBtn>
                                </div>
                            </div>
                        </Fragment>
                    </div>
                </div>
            </div>
        )
    }
}

export default RecommendationList