import React, { Fragment, Component } from 'react';

import Slider from "react-slick";
import Swipe from 'react-easy-swipe';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import api from "../../../util/api";

import RecommendationList from "./index";
import RecomendationCard from './RecomendationCard'

import eye from "../../../assets/img/recommendation/ic_round-remove-red-eye.svg";
import brain from "../../../assets/img/recommendation/mdi_brain.svg";
import spoonFork from "../../../assets/img/recommendation/si-glyph_spoon-fork.svg";
import sound from "../../../assets/img/recommendation/foundation_sound.svg";
import btnBurger from "../../../assets/img/recommendation/btn-burger.svg";
import profile from "../../../assets/img/recommendation/profile.svg";

import style from "../../../components/SuccessPage/success.module.css";

import  introStyle from  './intro.module.css'

import arrUp from '../../../assets/img/intro/arr_up.svg'
import arrDown from '../../../assets/img/intro/arr_down.svg'
import arrPointer from '../../../assets/img/intro/arr-pointer.svg'
import circle from '../../../assets/img/intro/circle.svg'


const Intro = ({setViewed}) => {


    return (
        <div className={introStyle.intro} onClick={setViewed}>
            <div className={introStyle.actions}>
                <img src={arrUp} alt=""/>
                <div className={introStyle.text}>
                    swipe screen up/down  to get recommendations
                </div>
                <img src={arrDown} alt=""/>
            </div>

            <div className={introStyle.center}>
                tap if  “im going”
                <div className={introStyle.circle} >
                    <img src={circle} alt=""/>
                </div>
            </div>

            <div className={introStyle.right}>
                change category what do
                you want now
                <div className={introStyle.rightPointer}>
                    <img src={arrPointer} alt=""/>
                </div>
            </div>
        </div>
    )
}


class RecomendationBase extends Component{

    constructor(props) {

        super(props)

        this.state = {
            recommendationList: [],
            categoryId: 0,
            currentRecommendation : null,
            categoriesVisible: false,
            introViewed: !!localStorage.getItem('introViewed'),
            categories: [
                {
                    id: null,
                    text: 'ALL'
                },
                {
                    id: 1,
                    icon: brain
                },
                {
                    id: 2,
                    icon: sound
                },
                {
                    id: 3,
                    icon: spoonFork
                },
                {
                    id: 4,
                    icon: eye
                }
            ],
            coords: null,
            currentSlide: 0
        }
    }

    componentDidMount() {


        if (!localStorage.getItem('navigation')) {
            this.getRecommendation()
        }

        navigator.geolocation.getCurrentPosition(({coords}) => {

            localStorage.setItem('navigation', 'granted')

            this.setState({
                coords
            }, () => {
                this.getRecommendation()
            })
        }, (e) => {

            console.log(e)

        }, {
            enableHighAccuracy: true,
            timeout: 50000,
            maximumAge: 0
        });
    }

    getRecommendation = () => {

        const { categoryId, coords } = this.state

        console.log('categoryId',categoryId)

        let params = {}

        if (categoryId) {
            params = {
                recommendationId: categoryId
            }
        }

        if (coords) {
            params = {
                ...params,
                currentLocation: {
                    lat: coords.latitude,
                    lan: coords.longitude
                }
            }
        }

        this.setState({
            pending: true
        })

        api.get(`recommendation`, {
            params
        }).then(({data}) => {

            this.setState({
                recommendationList: [...data, {
                    subscribeSlide: true
                }],
                pending: false
            })
        }).catch(() => {
            this.setState({
                pending: false
            })
        })
    }

    render() {

        const { recommendationList, categories, introViewed, currentRecommendation, currentSlide } = this.state

        const settings = {
            dots: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: false,
            vertical: true,
            verticalSwiping: true,
            arrows: false,
            currentSlide: 0,
            initialSlide: currentSlide,
            afterChange: (index) => {
                this.setState({
                    currentSlide: index
                })
            }
        };


        return (

            <Fragment>

                {!introViewed &&  <Intro setViewed={() => {

                        this.setState({
                            introViewed: true
                        })
                    localStorage.setItem('introViewed', 'viewed')

                }} /> }

                {currentRecommendation ? <RecomendationCard  {...currentRecommendation}
                                                             setCurrentRecomendation={() => {
                                                                 this.setState({
                                                                     currentRecommendation: null
                                                                 })
                                                             }}/> :

                    <Swipe onSwipeRight={() => {
                        this.props.onSetScreen()
                    }} tolerance = { 100 }>

                        <div>
                            <Slider {...settings} currentSlide={currentSlide}>
                                {recommendationList.map((item,i) => {

                                    console.log(item.id)

                                    return <RecommendationList {...item}
                                                               currentSlide={this.state.currentSlide}
                                                               setCurrentSlide={(currentSlide = 0) => {
                                                                   this.setState({
                                                                       currentSlide
                                                                   })
                                                               }}
                                                               setCurrentRecomendation={(data) => {
                                                                   this.setState({
                                                                       currentRecommendation: data
                                                                   })
                                                               }} key={`${item.id}-${i}`}/>
                                })}
                            </Slider>
                        </div>
                    </Swipe>
                }


                {this.state.categoriesVisible && <div className={style.catMenu}>
                    {categories.map((cat, i) => {
                        return (
                            <div key={i} className={style.catIcon} onClick={() => {
                                this.setState({
                                    categoryId: cat.id,
                                    categoriesVisible: false
                                }, () => {
                                    this.getRecommendation()
                                })
                            }}>
                                {cat.icon ? <img src={cat.icon} alt=""/> : cat.text}
                            </div>
                        )
                    })}
                </div>}

                {!currentRecommendation &&  <Fragment>
                    <button className={`${style.categoryBtn}  ${this.state.categoriesVisible ? `${style.categoryBtnActive}` : ''} `}
                            onClick={() => {
                                this.setState({
                                    categoriesVisible: !this.state.categoriesVisible
                                })
                            }}
                    >
                        {this.state.categoryId ?
                            <Fragment>
                                <img src={this.state.categories.find(({id}) => id === this.state.categoryId).icon} alt=""/>
                            </Fragment> : <img src={btnBurger} className={style.burger} alt=""/>
                        }
                    </button>
                    <button className={`${style.profileBtn} `} onClick={() => {
                            this.props.onSetScreen()}}
                    >
                        <img src={profile} alt=""/>
                    </button>
                </Fragment>}
            </Fragment>
        );
    }
}

export default RecomendationBase