import React, { Fragment, Component } from 'react';

import Slider from "react-slick";
import Swipe from 'react-easy-swipe';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
  // EmailShareButton,
  FacebookShareButton,
  InstapaperShareButton,
  // LineShareButton,
  // LinkedinShareButton,
  // LivejournalShareButton,
  // MailruShareButton,
  // OKShareButton,
  // PinterestShareButton,
  // PocketShareButton,
  // RedditShareButton,
  TelegramShareButton,
  // TumblrShareButton,
  TwitterShareButton,
  // ViberShareButton,
  // VKShareButton,
  WhatsappShareButton,
  // WorkplaceShareButton,
} from "react-share";

import {
  // EmailIcon,
  FacebookIcon,
  InstapaperIcon,
  // LineIcon,
  // LinkedinIcon,
  // LivejournalIcon,
  // MailruIcon,
  // OKIcon,
  // PinterestIcon,
  // PocketIcon,
  // RedditIcon,
  TelegramIcon,
  // TumblrIcon,
  TwitterIcon,
  // ViberIcon,
  // VKIcon,
  // WeiboIcon,
  WhatsappIcon,
  // WorkplaceIcon,
} from "react-share";

import api from "../../../util/api";
import style from "../../../components/SuccessPage/success.module.css";
import {GoogleMap, Marker, withGoogleMap, withScriptjs} from "react-google-maps";
import RecommendationList from "./index";
import faceLike from "../../../assets/img/recommendation/face-like.png";
import faceDislike from "../../../assets/img/recommendation/face-dislike.png";
import happyFace from "../../../assets/img/recommendation/happy-fave.svg";
import sadFace from "../../../assets/img/recommendation/sad-face.svg";
import share from "../../../assets/img/recommendation/share.svg";
import defaultBg from "../../../assets/img/01.gif";
// import defaultBg from "../../../assets/img/recommendation/subscribeBg.png";

import { ReactComponent as Prev } from "../../../assets/img/chevron-left.svg";
import { ReactComponent as Next } from "../../../assets/img/chevron-right.svg";


const MapWithAMarker = withScriptjs(withGoogleMap((props) =>
    <GoogleMap
        defaultZoom={16}
        defaultCenter={{ lat: parseFloat(props.lat), lng: parseFloat(props.lng) }}
    >
        <Marker
            position={{ lat: parseFloat(props.lat), lng: parseFloat(props.lng) }}
        />
    </GoogleMap>
))


export default class RecomendationCard extends Component {


    constructor(props) {
        super(props);

        this.state = {
            rate: false,
            step: 1,
            showSharing: false,
            stepLimit: 5,
            photos: this.props.item.posts,
            rateMark: null
        }
    }

    updateStep = (step) => {

        const { stepLimit } = this.state

        if (step >= stepLimit) {
            return
        }

        this.setState({
            step: step
        }, () => {
            if (this.state.step === 0) {
                this.props.setCurrentRecomendation()
            }
        })

    }


    setRate = (rate = true) => {
        this.setState({
            rate
        })
    }

    postRate = (mark) => {


        api.post('rate ', {
            locationId: '',
            value: mark
        })


        this.setState({
            rate: false,
            rateMark: mark
        })
    }

    setShare = (showSharing = true) => {
        this.setState({
            showSharing
        })
    }

    SharePanel = () => {

        const shareUrl = 'https://secure.stayandsmile.com';

        return (
            <div style={{
                width: '96%',
                margin: '7px',
                padding: '35px',
                background: 'white',
                position: 'absolute',
                zIndex: '9999',
                bottom: '5px',
                height: '55%'
            }}>
                <h4 style={{
                        marginBottom: '120px'
                }}>Поделить интересным местом с другом!</h4>
                <FacebookShareButton url={shareUrl} >
                    <FacebookIcon size={32} round={true} />
                </FacebookShareButton>
                <TelegramShareButton url={shareUrl} >
                    <TelegramIcon size={32} round={true} />
                </TelegramShareButton>
                <InstapaperShareButton url={shareUrl} >
                    <InstapaperIcon size={32} round={true} />
                </InstapaperShareButton>
                <TwitterShareButton url={shareUrl} >
                    <TwitterIcon size={32} round={true} />
                </TwitterShareButton>
                <WhatsappShareButton url={shareUrl} >
                    <WhatsappIcon size={32} round={true} />
                </WhatsappShareButton>
                <button className={style['closed']} onClick={() => {
                    this.setState({
                        showSharing: false
                    })
                }}>
                    ×
                </button>
            </div>
        )
    }

    render() {

        const { rate, step, showSharing, rateMark } = this.state

        const settings = {
            dots: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: false,
            vertical: true,
            verticalSwiping: true,
            arrows: false,
        };

        return (
            <div className={style.content}>
                {rateMark === 1 &&
                <div className={`${style.mark} ${style.markPositive}`}>
                    like
                </div>
                }

                {rateMark === 0 &&
                <div className={`${style.mark} ${style.markNegative}`}>
                    nope
                </div>
                }

                <Swipe onSwipeLeft={() => {

                    this.updateStep(this.state.step + 1)
                }} tolerance = { 100 }
                       onSwipeRight={() => {
                           this.updateStep(this.state.step - 1)
                       }}
                >

                    <div className={` ${style['content-inner']} ${style['detailed-content']}`} style={{
                        padding: 0
                    }}>

                        {rate &&
                        <div className={style.rate}>
                            <div className={style['rate-title']}>
                                rate this point
                            </div>
                            <div>
                                <a href="#" className={style['link-white']} onClick={(e) => {
                                    e.preventDefault()
                                    this.setRate(false)
                                }}>
                                    rate later
                                </a>
                            </div>
                            <div className={style['rate-faces']}>
                                <button className={style['rate-face-btn']} onClick={() => {
                                    this.setRate(false)
                                }}>
                                    <img src={happyFace} alt=""/>
                                </button>
                                <div className={style['rate-faces-sep']}>
                                    or
                                </div>
                                <button className={style['rate-face-btn']} onClick={() => {
                                    this.setRate(false)
                                }}>
                                    <img src={sadFace} alt=""/>
                                </button>
                            </div>

                            <div style={{
                                marginTop: 'auto',
                                marginBottom: '20px',
                            }}>
                            </div>
                        </div>
                        }

                        {
                            showSharing && this.SharePanel()
                        }

                        <div className={style['detailed-type']}>
                            Type: {this.props.item.type}
                        </div>

                        <div className={style['detailed-rating']}>
                            <div className={style.rating}>
                                <div>
                                    Рейтинг места
                                </div>
                                <div>
                                    <button className='button-clear-defaults'
                                            disabled={rateMark || rateMark === 0}
                                            onClick={() => this.postRate(1)}><img src={faceLike} alt=""/></button> +  {this.props.positiveReviews || 'N/A'}
                                </div>
                                <div>
                                    <button className='button-clear-defaults'
                                            disabled={rateMark || rateMark === 0}
                                            onClick={() => this.postRate(0)}><img src={faceDislike} alt=""/></button> - {this.props.negativeReviews || 'N/A'}
                                </div>
                            </div>
                        </div>
                        <Slider {...settings}>

                            {this.state.photos.length > 0 ?
                                <Fragment>
                                    {this.state.photos.map((post) => {
                                        return post && (
                                            <Fragment key={post.photo}>
                                                <img src={post.photo} alt=""
                                                     style={{display: 'none'}}
                                                     onError={() => {
                                                         this.setState({
                                                             photos: this.state.photos.filter((post) => post && post.photo !== post.photo)
                                                         })
                                                     }}/>
                                                <div className={style.wrap}  style={{
                                                    backgroundImage: `url(${post.photo}), url(${defaultBg})`
                                                }}>
                                                    <div className={style['detailed-block-main']}>
                                                        <div style={{
                                                            color: '#C432AC'
                                                        }}>
                                                            {post.description}
                                                        </div>
                                                    </div>
                                                </div>

                                            </Fragment>
                                        )
                                    })}
                                </Fragment>:
                                <Fragment>
                                    <div className={style.wrap}  style={{
                                        backgroundImage: `url(${this.props.item.mainphoto}), url(${defaultBg})`
                                    }}>
                                        <div className={style['detailed-block-main']}>
                                            <div style={{
                                                color: '#C432AC'
                                            }}>
                                                {this.props.item.description}
                                            </div>
                                        </div>
                                    </div>
                                </Fragment>
                            }

                        </Slider>
                    </div>

                    {step > 1 &&
                        <Fragment>
                            <div className={style['detailed-list']}>
                                <div style={{textAlign: 'right'}}>
                                    <div className={style['cross']} onClick={() => {
                                        this.setState({
                                            step: 1
                                        })
                                    }}>
                                        ×
                                    </div>                                    
                                    <div className={style['cross']} onClick={() => {
                                        this.setState({
                                            showSharing: true
                                        })
                                    }}>
                                        <img className={style['share']} src={share} alt=""/>
                                    </div>
                                </div>

                                {step === 2 &&
                                    <Fragment>
                                        
                                        <div className={style['detailed-block']}>
                                            <div style={{
                                                fontSize: '1.25rem'
                                            }}>
                                                {this.props.item.title}
                                            </div>                   
                                            <div style={{
                                                fontSize: '1.25rem'
                                            }}>
                                                {this.props.item.address.replace('Россия, Москва','')}
                                            </div>
                                            <div style={{
                                                color: '#7935CB',
                                                fontWeight: 300
                                            }}>
                                                {this.props.latitude}, {this.props.longitude}
                                            </div>
                                            <div style={{
                                                fontSize: '1.25rem'
                                            }}>
                                                {this.props.item.workhours}
                                            </div>
                                            <div style={{
                                                color: '#FEBD38',
                                            }}>
                                                {this.props.averageBillUSD > 0 ? `средний чек : ${this.props.averageBillUSD}₽` : 'FREE'}
                                            </div>
                                        </div>                     
                                        <div className={style['detailed-block']}>
                                            <div style={{
                                                marginBottom: '4px'
                                            }}>
                                                <a href= {this.props.website} target="_blank"
                                                   style={{
                                                       color: '#7935CB',
                                                   }}
                                                   rel="noopener nofollow">
                                                    {this.props.item.website}
                                                </a>
                                            </div>
                                            <div style={{
                                                marginBottom: '4px'
                                            }}>
                                                {this.props.phoneNumber}
                                            </div>
                                        </div>
                                        <div className={style['detailed-block']}>
                                            {this.props.description}
                                        </div>
                                    </Fragment>
                                }
                                {step === 3 &&
                                <div>
                                    <MapWithAMarker
                                        googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyB8weNak3605tIrBrKaNi3Tr1A9AGzVxsE"
                                        loadingElement={<div style={{ height: `100%` }} />}
                                        containerElement={<div style={{ height: `400px` }} />}
                                        mapElement={<div style={{ height: `100%` }} />}
                                        lat={this.props.latitude} lng={this.props.longitude}/>
                                </div>
                                }


                            </div>
                        </Fragment>
                    }
                    <button className={`${style.nav} ${style['nav-prev']}`} onClick={() => {
                        this.updateStep(this.state.step - 1)
                    }}>
                        <Prev />
                    </button>
                    <button className={`${style.nav} ${style['nav-next']}`} onClick={() => {
                        this.updateStep(this.state.step + 1)
                    }}>
                        <Next />
                    </button>
                </Swipe>
            </div>
        )


        return (
            <Fragment>
                <div />

            </Fragment>
        )
    }

}